<template>
    <div class="main-view">
        <div class="flex-row flex-wrap">
            <div v-for="item in videoList" :key="item.deviceId">
                <video class="video-item" :id="item.deviceId" autoplay></video>
            </div>
        </div>
        <el-button type="primary" @click="capture()">点击拍照</el-button>
        <div class="flex-row flex-wrap">
            <div v-for="item in videoList" :key="item.deviceId">
                <canvas class="image-item" :id="'canvas' + item.deviceId"></canvas>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            videoList: []
        };
    },
    created() {
        this.getDevice();
    },

    methods: {
        getDevice() {
            if (navigator.mediaDevices === undefined) {
                navigator.mediaDevices = {};
            }
            if (navigator.mediaDevices.getUserMedia === undefined) {
                navigator.mediaDevices.getUserMedia = function (constraints) {
                    // 首先，如果有getUserMedia的话，就获得它
                    var getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
                    // 一些浏览器根本没实现它 - 那么就返回一个error到promise的reject来保持一个统一的接口
                    if (!getUserMedia) {
                        return Promise.reject(new Error("getUserMedia is not implemented in this browser"));
                    }
                    // 否则，为老的navigator.getUserMedia方法包裹一个Promise
                    return new Promise(function (resolve, reject) {
                        getUserMedia.call(navigator, constraints, resolve, reject);
                    });
                };
            }
            if (navigator.mediaDevices.enumerateDevices) {
                navigator.mediaDevices.enumerateDevices().then(
                    devices => {
                        let list = devices.filter(i => i.deviceId && i.kind == "videoinput");
                        this.videoList = list;
                        list.forEach(item => {
                            this.srartMedia(item.deviceId);
                        });
                    },
                    err => {
                        console.log(err.name + ": " + err.message);
                    }
                );
            }
        },

        srartMedia(deviceId) {
            if (!deviceId) {
                return;
            }
            var constraints = {
                video: {
                    deviceId: deviceId
                },
                audio: true
            };
            console.log("deviceid", deviceId);
            navigator.mediaDevices
                .getUserMedia(constraints)
                .then(stream => {
                    var video = document.getElementById(deviceId);
                    try {
                        video.srcObject = stream;
                    } catch (error) {
                        video.src = window.URL.createObjectURL(stream);
                    }
                })
                .catch(err => {
                    console.log(err.name + ": " + err.message);
                });
        },

        capture() {
            let videoList = this.videoList;
            for (let i = 0; i < videoList.length; i++) {
                let canvas = document.getElementById("canvas" + videoList[i].deviceId);
                var cxt = canvas.getContext("2d");
                var video = document.getElementById(videoList[i].deviceId);
                canvas.width = video.videoWidth;
                canvas.height = video.videoHeight;
                cxt.drawImage(video, 0, 0, canvas.width, canvas.height);
                // var data = canvas.toDataURL("image/png");
            }
        }
    }
};
</script>

<style scoped>
.main-view {
    width: 100vw;
    height: 100vh;
}
.video-item {
    width: 4rem;
    margin: 0.1rem;
}
.image-item {
    width: 4rem;
    margin: 0.1rem;
}
</style>
